<template>
	<view class="box">
		<!-- 此处要做banner -->
		<view class="banner-box">
			<!-- 此处要做banner -->
			<area-banner :type="'make_money'" :tips_show="false"></area-banner>
		</view>
		<view class="type-list">
			<view class="cate-box">
				<template v-for="(val,k,i) in rule_map">
					<view class="type-li" :class="{'active':k==ruleSort}" @tap="chooseRule(k)" :key="i">
						{{val}}
					</view>
				</template>
			</view>
		</view>
		<mescroll-body ref="mescrollRef" @init="mescrollInit" @down="downCallback" @up="upCallback" :class="{'hidden':!location_allow}">
			<view class="reward-box">
				<template v-for="(item,k) in data_list">
					<view url="/pages/make_money/article_detail" class="reward-li" :key="k">
						<navigator :url="'/pages/make_money/article_detail?seqId='+item.articleSeqId+'&offerRewardSeqId='+item.seqId"
						 class="top">
							<image lazy-load class="img" :src="item.picUrl" mode="aspectFill"></image>
							<view class="right">
								<view class="title">
									{{item.articleTitle}}
								</view>
								<view class="reward-money">
									<view class="money">
										<text>悬赏金额</text>
										<image lazy-load class="img" src="../../static/images/gold.png" mode="aspectFill"></image>
										<text class="text">{{item.rewardTotalMoney | formatNum}}</text>
									</view>
									<view class="money">
										<text>余额</text>
										<image lazy-load class="img" src="../../static/images/gold.png" mode="aspectFill"></image>
										<text class="text">{{item.surplusMoney | formatNum}}</text>
									</view>
									<view>
									</view>
								</view>
								<progressBar2 :percent="item.percentage+'%'"></progressBar2>
								<view class="share-money">
									<view class="share-li">
										分享赚<text class="text-red">{{item.shareMoney | formatNum}}</text>金币
									</view>
									<view class="share-li">
										阅读赚<text class="text-red">{{item.readingMoney | formatNum}}</text>金币
									</view>
									<template v-if="item.publishedHour||item.publishedMinute">
										<view class="share-li" v-if="item.publishedHour>0">
											{{item.publishedHour}}小时前
										</view>
										<view class="share-li" v-else>
											{{item.publishedMinute}}分钟前
										</view>
									</template>
									<template v-else>
										<view class="share-li" v-if="item.releaseTime!=null">
											{{item.releaseTime}}
										</view>
									</template>
								</view>
							</view>
						</navigator>
						<view class="bottom">
							<view class="left">
								<view class="name">{{item.issuedByName}}</view>
								<view class="is_share" v-if="item.remainingTime!==null">
									<text class="iconfont icontishi"></text> {{item.remainingTime}}小时后再次分享享奖励
								</view>
							</view>
							<view class="" v-if="item.remainingTime==null">
								<view class="no-btn" @tap="openDetail(k)">
									<view class="bg-btn make-btn">
										分享赚钱
									</view>
								</view>
							</view>
						</view>
					</view>
				</template>
			</view>
		</mescroll-body>
		<template v-if="!location_allow&&!lon1">
			<view class="no-address">
				<view class="text">
					无法定位，请授权位置服务
				</view>
				<view class="border-btn" @tap="locationFail('err',2)">
					授权定位
				</view>
			</view>
		</template>

		<navigator url="/pages/make_money/issue_reward" class="release_reward">
			<view class="iconfont iconfenxiangzhuanqian">
				<view class="text">
					发布悬赏
				</view>
			</view>
		</navigator>
		
	</view>
</template>

<script>
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollBody from "@/components/mescroll-uni/mescroll-body.vue"
	import areaBanner from '@/components/area-banner.vue'
	import progressBar2 from '../../components/progress-bar2.vue'
	let page_index=0;
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				ruleSort: "NEWEST", // NEWEST TOTAL_AMOUNT SHARE READ,
				rule_map: {
					'NEWEST': "最新悬赏",
					'TOTAL_AMOUNT': "悬赏总额",
					'SHARE': "分享奖励",
					'READ': "阅读奖励",
				},
				data_list: [],
				location_allow: false,
				lon1: "1",
				lat1: "",
				share_offerRewardSeqId:"",
				canReset :false
			}
		},
		components: {
			areaBanner,
			progressBar2,
			MescrollBody
		},
		methods: {
			loadNewData(more){
				if(more){
					// 上拉加载更多
					if(this.data_list.length==0&&page_index>0)return;
					this.mescroll.triggerUpScroll();
				}else {
					this.canReset&&this.mescroll.resetUpScroll()
					this.canReset = true // 过滤第一次的onShow事件,避免初始化界面时重复触发upCallback
				}
			},
			openDetail(k){
				uni.navigateTo({
					url:'/pages/make_money/article_detail?seqId='+this.data_list[k].articleSeqId+'&offerRewardSeqId='+this.data_list[k].seqId+'&sharebtn=true'
				})
			},
			// 获取当前地址
			getNowLocation() {
				let self = this;
				uni.getLocation({
					type: 'gcj02',
					success: function(res) {
						console.log(res);
						self.location_allow = true;
						if (!self.lon1 || !self.lat1) {
							self.lon1 = res.longitude
							self.lat1 = res.latitude;
							self.data_list = [];
							self.mescroll.resetUpScroll(true);
						}
					},
					fail: function(errMsg) {
						self.locationFail(errMsg)
					},
				});
			},
			// 获取位置权限 time 未获取到位置信息再次点击获取
			locationFail(err, time = 1) {
				let self = this;
				if (err.errMsg === "getLocation:fail auth deny" || err.errMsg === "getLocation:fail authorize no response" || time ==
					2) { // 没有授权，重新授权，
					uni.showModal({
						title: '授权提示',
						content: "是否允许开启位置信息",
						success: (res) => {
							if (res.confirm) { // 点击确定，则调用位置授权
								uni.openSetting({
									success(settingdata) {
										self.location_allow = true;
										if (settingdata.authSetting["scope.userLocation"]) {
											uni.showToast({
												title: '获取权限成功'
											});
											console.log('获取权限成功');
											self.mescroll.triggerUpScroll();
										} else {
											self.location_allow = false;
											self.lon1 = "";
											self.address = '获取失败';
											console.log("获取权限失败")
										}
									}
								})
							}
							if (res.cancel) {
								self.location_allow = false;
								self.lon1 = "";
								self.address = '获取失败';
								console.log("取消获取权限")
							}
						}
					})
				}
			},
			getData() {
				this.getNowLocation();
			},
			upCallback(page) {
				if(!this.lat1){
					return
				}
				let pageNum = page.num; // 页码, 默认从1开始
				let pageSize = page.size; // 页长, 默认每页10条
				page_index=pageNum
				this.$api.get('/o2oOfferReward/findByRuleSortPage', {
					params: {
						ruleSort: this.ruleSort,
						pageno: pageNum,
						pagesize: pageSize,
						lon1: this.lon1,
						lat1: this.lat1,
						customerSeqId:this.$common.getLocalSync('customerSeqId')
					}
				}).then((res) => {
					if (res.code && res.code != 200) {
						this.mescroll.endErr();
						return
					}
					let data = res;
					// 接口返回的当前页数据列表 (数组)
					let curPageData = data.list;
					// 接口返回的当前页数据长度 (如列表有26个数据,当前页返回8个,则curPageLen=8)
					let curPageLen = curPageData.length;
					// 接口返回的总页数 (如列表有26个数据,每页10条,共3页; 则totalPage=3)
					let totalPage = data.pages;
					for(let item of curPageData){
						if(item.releaseTime&&item.releaseTime!=null){
							item.releaseTime=item.releaseTime.slice(5,-9)
						}
					}
					//设置列表数据
					if (page.num == 1) this.data_list = []; //如果是第一页需手动置空列表
					this.data_list = this.data_list.concat(curPageData)
					// 请求成功,隐藏加载状态
					//方法一(推荐): 后台接口有返回列表的总页数 totalPage
					this.mescroll.endByPage(curPageLen, totalPage);
				})
			},
			chooseRule(k) {
				this.ruleSort = k;
				this.mescroll.resetUpScroll(false)
			},
		}
	}
</script>

<style lang="scss">
	.hidden {
		display: none !important;
	}

	.release_reward {
		position: fixed;
		z-index: 1000;
		top: calc(var(--window-top) + 400rpx);
		right: 0;
		width: 124rpx;
		height: 124rpx;
		background: rgba(255, 157, 151, 1);
		border-radius: 50%;
		color: white;
		font-size: 18rpx;
		text-align: center;
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		justify-content: center;
		box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.2);

		.iconfont {
			font-size: 56rpx;
			margin-top: -8rpx;

			.text {
				font-size: 18rpx;
				width: 100%;
				text-align: center;
				margin-top: -8rpx;
			}
		}

	}

	.reward-box {
		width: 100%;
		height: auto;

		.reward-li {
			width: 652rpx;
			padding: 0 30rpx 6rpx 0;
			background: white;
			margin: 14rpx auto;
			border-radius: 6rpx;


			.top {
				width: 100%;
				overflow: hidden;
				display: flex;
				justify-content: space-between;
				overflow: hidden;

				.img {
					width: 200rpx;
					height: 148rpx;
					flex-shrink: 0;
					background: rgba(255, 255, 255, 1);
					border-radius: 4px;
					// box-shadow: 0 0 11px 10px #901818;
					box-shadow: 0 0 14rpx 2rpx #bbbaba;
					margin: 30rpx 22rpx 14rpx 30rpx;
				}

				.right {
					width: 400rpx;
					margin-top: 30rpx;
					.title {
						width: 100%;
						font-size: 26rpx;
						font-weight: 500;
						color: rgba(51, 51, 51, 1);
						margin-bottom: 14rpx;
						overflow: hidden;
						text-overflow: ellipsis;
						white-space: nowrap;
					}
				}

				.reward-money {
					display: flex;
					justify-content: space-between;
					align-items: center;
					font-size: 18rpx;
					color: #666666;
					margin-bottom: 10rpx;

					.img {
						width: 20rpx;
						height: 20rpx;
						margin: 0 10rpx;
						box-shadow: none;
					}

					.text {
						font-size: 16rpx;
						color: #FFB429;
					}
				}

				.share-money {
					display: flex;
					justify-content: space-between;
					// #ifdef H5
					flex-wrap: wrap;
					// #endif
					font-size: 18rpx;
					color: #999999;
					margin-top: 10rpx;
				}
			}

			.bottom {
				display: flex;
				font-size: 20rpx;
				color: #656565;
				align-items: flex-start;
				justify-content: space-between;
				overflow: hidden;
				margin-top: 10rpx;
				height: 66rpx;
				margin-left: 30rpx;

				.left {

					.name {
						font-size: 22rpx;
						color: #666666;
					}

					.is_share {
						color: #FF7765;
						display: flex;
						align-items: center;

						.iconfont {
							margin-right: 10rpx;
						}
					}
				}

			}
		}
	}

	.make-btn {
		width: 156rpx;
		height: 48rpx;
		background: linear-gradient(90deg, rgba(255, 158, 151, 1) 0%, rgba(255, 54, 44, 1) 100%);
		border-radius: 23rpx;
		padding: 0;
		display: flex;
		align-items: center;
		justify-content: center;

	}

	.type-list {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		font-size: 26rpx;
		position: relative;
		z-index: 2;
		margin-bottom: 30rpx;

		.cate-box {
			width: 686rpx;
			margin: 0 auto;
			display: flex;
			overflow: auto hidden;
		}

		.type-li {
			min-width: 88rpx;
			flex-shrink: 0;
			position: relative;
			overflow-y: hidden;
			display: inline-block;
			text-align: center;
			margin-right: 50rpx;
		}

		.active {
			font-size: 30rpx;
			font-weight: bold;

			&::after {
				content: "";
				position: absolute;
				bottom: 0;
				left: 50%;
				width: 80%;
				height: 12rpx;
				background: $color-primary;
				border-radius: 6rpx;
				z-index: -1;
				transform: translateX(-50%);
			}
		}
	}
</style>
